@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');



.coin-app {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 64px;
  color: #fff;
}

.coin-search {
  margin-bottom: 64px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
}

.coin-text {
  margin-bottom: 32px;
  text-align: center;
  font-size: 44px;
  font-weight: 600;
}

.coin-input {
  padding-left: 16px;
  width: 300px;
  height: 50px;
  border-radius: 4px;
  border: none;
  background-image: linear-gradient(-225deg,
      #ac32e4 0%,
      #7918f2 48%,
      #4801ff 100%);

  color: #e2e2e2;
  outline: 0;
  font-size: 18px;
}

.coin-input::placeholder {
  color: #e2e2e2;
  font-size: 18px;
}